import React from 'react';
import { graphql } from 'gatsby';

import Layout from '../components/Layout';
import Banner from '../components/Banner';
import Container from '../components/Container';
import BlockAboutHeader from '../components/BlockAboutHeader';
import SpaceDivider from '../components/SpaceDivider';
import Cards from '../components/Cards';
import Footer from '../components/Footer';

import { edgesByName } from '../utils/gql';

export const pageQuery = graphql`
  {
    images: allFile(filter: { relativeDirectory: { eq: "integrations" } }) {
      edges {
        node {
          name
          childImageSharp {
            fluid {
              base64
              tracedSVG
              srcWebp
              srcSetWebp
              originalImg
              originalName
            }
          }
        }
      }
    }
  }
`;

export default function IntegrationsPage({ data }) {
  const images = edgesByName(data.images);

  console.log('IntegrationsPage', images);

  return (
    <Layout>
      <Banner />
      <Container>
        <BlockAboutHeader
          title="Integrate payment terms seamlessly into your checkout"
          desc={
            <p>
              Our API supports flexible customizations, allowing you to offer
              terms in all of your existing sales channels.
              <br />
              <br />
              <br />
            </p>
          }
        />
        <SpaceDivider height="75" />
        <Cards
          title="Platform integrations"
          subtitle={<span />}
          hideRightArrow
          cards={[
            {
              href:
                'https://docs.resolvepay.com/docs/platform-integrations/magento-2',
              title: '',
              content: (
                <div>
                  <div className="integration-logo">
                    <img
                      src={images['magento2-logo'].originalImg}
                      alt="Magento2"
                    />
                  </div>
                  <br />
                  <br />
                  Magento is packed with B2B
                  <br />
                  features that drive growth and
                  <br />
                  has an open-source ecosystem.
                  <br />
                  The Resolve Extension allows
                  <br />
                  your business customers to
                  <br />
                  apply for terms and checkout
                  <br />
                  without leaving your online store.
                  <br />
                  <br />
                </div>
              ),
            },
            {
              href: 'https://app.resolvepay.com/docs/api/',
              title: '',
              content: (
                <div>
                  <div className="integration-logo">
                    <img
                      src={images['resolve-direct-api'].originalImg}
                      alt="Resolve Direct API"
                    />
                  </div>
                  <br />
                  <br />
                  Resolve's flexible API can be
                  <br />
                  integrated into any platform
                  <br />
                  Allow your business customers to
                  <br />
                  apply for terms and checkout
                  <br />
                  without leaving your online store.
                  <br />
                  <br />
                </div>
              ),
            },
          ]}
        />
      </Container>
      <Footer />
    </Layout>
  );
}
